/* Typography */
body {
    font-family: Arial, sans-serif;
    font-size: 14px;
    line-height: 1.6;
    color: #333;
  }
  
  h1,
  h2,
  h3 {
    margin: 0;
    padding: 0;
  }
  
  h1 {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  /* Container */
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
  
  /* Header */
  .header {
    background-color: #f5f5f5;
    padding: 20px 0;
  }
  
  .header h1 {
    color: #333;
  }
  
  /* Navigation */
  .navbar {
    background-color: #333;
    color: #fff;
    padding: 10px;
  }
  
  .navbar ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .navbar ul li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .navbar ul li a {
    color: #fff;
    text-decoration: none;
    padding: 5px 10px;
  }
  
  .navbar ul li a:hover {
    background-color: #666;
  }
  
  /* Hero Section */
  .hero {
    background-color: #003652;
    background-size: cover;
    background-position: center;
    color: #fff;
    padding: 150px 0;
    text-align: center;
  }
  
  .hero h1 {
    font-size: 42px;
    margin-bottom: 20px;
  }
  
  .hero p {
    font-size: 18px;
    margin-bottom: 30px;
  }
  
  /* Features Section */
  .features {
    margin: 50px 0;
  }
  
  .features h2 {
    text-align: center;
  }
  
  .features ul {
    margin-top: 30px;
    padding-left: 0;
    list-style: none;
  }
  
  .features ul li {
    margin-bottom: 10px;
  }
  
  .features ul li i {
    margin-right: 10px;
  }
  
  /* Footer */
  .footer {
    background-color: #333;
    color: #fff;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer p {
    margin: 0;
  }
  
  .footer a {
    color: #fff;
    text-decoration: none;
  }
  
  .footer a:hover {
    text-decoration: underline;
  }
  