.calculator-container {
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    background-color: #f1f1f1;
  }
  
  .input-container {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 20px;
  }
  
  .input-field {
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
  
  .input-icon {
    margin-right: 5px;
  }
  
  .calculator-button {
    margin-bottom: 10px;
  }
  
  .button-icon {
    margin-right: 5px;
  }
  
  .result {
    margin-top: 20px;
    font-weight: bold;
  }
  