.navigation {
    display: flex;
    background-color: #f0f0f0;
    padding: 20px;
  }
  
  .ui-container {
    flex: 1;
    padding-right: 20px;
  }
  
  .ui-heading {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .ui-description {
    font-size: 14px;
    color: #777;
    margin-bottom: 20px;
  }
  
  .navigation-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  
  .navigation-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .navigation-link {
    text-decoration: none;
    color: #333;
    transition: color 0.3s;
  }
  
  .navigation-link:hover {
    color: #007bff;
  }
  
  .navigation-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: background-color 0.3s;
    cursor: pointer;
  }
  
  .navigation-box:hover {
    background-color: #f5f5f5;
  }
  
  .navigation-icon {
    font-size: 24px;
    margin-bottom: 8px;
  }
  
  .navigation-text {
    font-size: 14px;
    font-weight: bold;
    text-align: center;
  }
  