.crypto-cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .crypto-card {
    background-color: #f8f8f8;
    border-radius: 8px;
    padding: 16px;
    margin: 8px;
    text-align: center;
    width: 200px;
  }
  
  .crypto-card__title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .crypto-card__price {
    font-size: 20px;
    font-weight: bold;
  }
  